//--- IMPORT ---------------------------------//
@import 'hexi-flexi-grid';

@mixin boardHexGridTemplate($size, $gridWidth) {
  //--- SETTINGS -------------- VALUES ---------//
  $gridWidth:   $gridWidth; // 'auto',(px,pt,r/em,vw/h)
  $gridHeight:  auto;       // 'auto',(px,pt,r/em,vw/h)
  $columnCount: $size;      // 'auto',(number)
  $rowCount:    $size;      // 'auto',(number)
  $hexCount:    auto;       // 'auto',(number)
  $hexLayout:   row;        // 'row','column','strict'
  $gridOrient:  vertical;   // 'vertical','horizontal'
  $crop:        none;       // 'none','crop'
  $cropFactor:  1;          // (number)
  $hexContent:  auto;       // 'auto','center'
  $hexSize:     auto;       // 'auto',(px,pt,r/em,vw/h)
  $hexMargin:   0.5em;      // (px,pt,r/em,vw/h)
  $hexShape:    hexagon;    // 'hexagon','circle'
  $hexColor:    #48a999;    // hexcode,rgb/a,named
  $images:      none;
  //--- MIXINS ---------------------------------//
  @include hexWrapper($gridWidth,$gridHeight,
      $gridOrient,$rowCount,$columnCount);
  .hexCrop {@include hexCrop($crop,$cropFactor)}
  .hexGrid {@include hexContainer($gridWidth,$gridHeight,
      $gridOrient,$columnCount, $images,$rowCount,
      $hexLayout,$hexContent,$hexShape,$hexColor,
      $hexMargin,$hexCount,$hexSize,)}
  //--- CUSTOM STYLES --------------------------//

  margin: 2em;
  //--------------------------------------------//
}

$gridWidth: 35em;

#boardHexGrid-1 {
  @include boardHexGridTemplate(3, $gridWidth);
}

#boardHexGrid-2 {
  @include boardHexGridTemplate(5, $gridWidth);
}

#boardHexGrid-3 {
  @include boardHexGridTemplate(7, $gridWidth);
}

#boardHexGrid-4 {
  @include boardHexGridTemplate(9, $gridWidth);
}
